.w {
  margin: auto;
}
.wide .w {
  width: 1240px;
}




.header {
  .header_redbg {
    width: 100%;
    height: 4px;
    margin-bottom: 6px;
    background: #e1251b;
  }

  .header_wrap {
    position: relative;
    height: 90px;
  }

  .header_wrap_logo {
    width: 126px;
    height: 70px;
    padding: 0 5px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }

  .header_wrap_nav {
    padding: 44px 0 0 0;
    overflow: hidden;
  }
}